@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@400;500&family=Roboto:wght@300;400;700&family=Ubuntu:ital,wght@0,400;0,500;0,700;1,300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  /* color */
  --main-color: rgb(0, 156, 246);
  --secondery-color: rgb(98, 98, 98);
  /* font */
  --ubuntu: "Ubuntu", sans-serif;
  --poppins: "Poppins", sans-serif;
  --roboto: "Roboto", sans-serif;
}
* {
  font-family: "Roboto", sans-serif;
  font-family: var(--roboto);
}
body::-webkit-scrollbar {
  width: 10px; /* width of the entire scrollbar */
}
body::-webkit-scrollbar-track {
  background: rgb(0, 0, 0); /* color of the tracking area */
}

body::-webkit-scrollbar-thumb {
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color); /* color of the scroll thumb */
  border-radius: 10px; /* roundness of the scroll thumb */
  border: 1px solid transparent; /* creates padding around scroll thumb */
}

.bg-navbar {
  background-color: rgb(0, 81, 173);
}
.brand-align {
  display: block;
}
.logo-name {
  color: rgb(0, 156, 246);
  color: var(--main-color);
  font-family: "Ubuntu", sans-serif;
  font-family: var(--ubuntu);
  font-size: 30px;
  font-weight: bold;
}
.home-container {
  position: relative;
  height: 70vh;
  width: 100%;
  background-image: url(/static/media/dev1.9add4eb9.jpg);
  background-size: cover;

  object-fit: cover;
}

.opacity-bg {
  height: 100%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.774);
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.title {
  width: calc(100vw / 2);
  font-size: 40px;
  text-align: center;
  color: white;
  font-weight: bold;
  margin: 25px;
  /* background-color: rgba(0, 0, 0, 0.89); */
}
@media (max-width: 1000px) {
  .title {
    width: calc(100vw / 1.3);
  }
}
.button {
  outline: none;
  border: none;
  padding: 10px;
  width: 200px;
  height: 50px;
  font-size: 20px;
  font-weight: bold;
  color: white;
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color);
  border-radius: 10px;
  cursor: pointer;
  transition: 0.2s;
}
.button:hover {
  background-color: rgb(7, 130, 246);
  font-size: 22px;
}

@media (max-width: 650px) {
  .home-container {
    background-position: center;
    height: 40vh;
  }
  .opacity-bg {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
  }
  .title {
    font-size: 20px;
    margin: 25px;
  }
  .button {
    padding: 10px;
    width: 150px;
    height: 50px;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
  }
}

/* About us */

.about-container {
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color);
  padding: 20px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.about-content-div {
  padding: 10px;
  width: calc((99vw / 1.5));
  background-color: white;
  border-radius: 15px;
  margin: 20px 0;
}

@media (max-width: 800px) {
  .about-content-div {
    width: calc((100vw / 1.2));
  }
}
.about-content-div p {
  margin: 10px 20px;
  color: rgb(98, 98, 98);
  color: var(--secondery-color);
  font-size: 18px;
  text-align: justify;
}

/* Section Title */
.section-div {
  display: inline;
  justify-content: center;
  margin: 40px 0;
}

.section-title {
  /* top: 0;
  left: 50%; */
  /* transform: translateX(-50%); */
  color: white;
  font-size: 35px;
  font-family: "Ubuntu", sans-serif;
  font-family: var(--ubuntu);
  font-weight: lighter;
  position: relative;
  display: inline;
  line-height: 0;
}
.section-title.blue {
  color: rgb(0, 156, 246);
  color: var(--main-color);
}
.section-title::after {
  content: "";
  position: absolute;
  text-align: center;
  width: 80%;
  left: 50%;
  height: 3px;
  -webkit-transform: translateX(-50%);
          transform: translateX(-50%);
  display: flex;
  flex-direction: column;
  background-color: rgb(255, 255, 255);
  border-radius: 50px;
}
.section-title.blue::after {
  background-color: rgb(0, 156, 246);
  background-color: var(--main-color);
}

/* carousel */

.carousel-div {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.all-carousel {
  width: 80%;
  display: inline-flex;
  justify-content: center;
  height: auto;
}
.div-per-carousel {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 80vw;
}
.carousel-div p {
  text-align: center;
  font-size: 25px;
  color: white;
}
.carousel-image-div {
  margin: 10px;
  height: 600px;
  width: 800px;
  background: white;
  border-radius: 10px;
  overflow: hidden;
  text-align: center;
  padding: 10px;
}

.carousel-image {
  width: 100%;
  height: 80%;
  background-color: burlywood;
}
.carousel-image img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.carousel-text {
  font-size: 25px;
  background-color: rgb(240, 240, 240);
  margin: 20px;
  color: rgb(98, 98, 98);
  color: var(--secondery-color);
}

/* contact us */
.contact-us {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.cantact-all-info {
  margin: 40px;
  width: 50%;
  display: flex;
  justify-content: space-around;
}
.cantact-us-text {
  width: 30%;
  text-align: center;
  font-size: 20px;
  color: rgb(98, 98, 98);
  color: var(--secondery-color);
}
.info-content {
  text-align: center;
}
.info-icon {
  color: rgb(0, 156, 246);
  color: var(--main-color);
  font-size: 50px;
}
.info-text {
  color: rgb(98, 98, 98);
  color: var(--secondery-color);
  font-size: 20px;
}
.info-text.email {
  color: rgb(0, 156, 246);
  color: var(--main-color);
  cursor: pointer;
}
@media (max-width: 650px) {
  .cantact-all-info {
    flex-direction: column;
  }
  .cantact-us-text {
    width: 90%;
  }
}
/* footer */
.footer-div {
  width: 100%;
  padding: 20px;
  text-align: center;
  background-color: rgb(224, 224, 224);
}
.footer-text {
  color: rgb(98, 98, 98);
  color: var(--secondery-color);
}
.modal-div {
  height: 80vh;
  width: 100%;
}
.iframe {
  width: 100%;
  height: 100%;
}
.nav-shadow {
  box-shadow: 5px 10px #888888;
  background-color: red;
  padding: 10px;
}

/* at your service */
.at-your-service {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding-bottom: 10%;
}
.service-all-card {
  margin-top: 50px;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  width: 95%;
}
.service-card {
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
  margin: 15px 10px;

  width: calc(100vw / 4);
}
.icon-bg {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100px;
  width: 100px;
  background-color: rgb(251, 251, 251);
  border-radius: 50%;
  transition: 0.3s;
}

.icon-bg:before {
  display: flex;
  content: "";
  position: absolute;
  height: 100px;
  width: 100px;
  border-radius: 50%;
  border: 3px solid var(--main-color);
  transition: 0.3s ease-in-out;
}
.icon {
  font-size: 30px;
  transition: 0.2s ease-in;
}

.icon-bg:hover {
  background-color: var(--main-color);
}
.icon-bg:hover .icon {
  color: white;
  font-size: 40px;
  font-weight: bold;
}

.icon-bg:hover::before {
  -webkit-animation: wave 0.5s ease-in-out;
          animation: wave 0.5s ease-in-out;
}
@-webkit-keyframes wave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.6);
            transform: scale(1.6);
    border: 3px solid #1dced100;
  }
}
@keyframes wave {
  0% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
  100% {
    -webkit-transform: scale(1.6);
            transform: scale(1.6);
    border: 3px solid #1dced100;
  }
}

.ceyan {
  color: var(--main-color);
}

.service-title {
  position: relative;
  margin-top: 20px;
  font-size: 25px;
  color: var(--secondery-color);
  margin-bottom: 10px;
}

.service-subtitle {
  font-size: 20px;
  color: var(--secondery-color);
  width: 95%;
}

@media (max-width: 900px) {
  .service-card {
    width: 95%;
  }
  .service-all-card {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

/* our works */
.our-works {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  background-color: var(--main-color);
  padding: 20px;
}
/* .all-works {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
} */

.work-div {
  margin: 10px;
  padding: 10px;
  width: 65%;
  height: auto;
  background-color: rgb(246, 246, 246);
  display: flex;
  /* flex-direction: column; */
  /* justify-content: center; */
  /* align-items: center; */
}

.work-image {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 60%;
  margin: 5px;
  height: 100%;
  background-color: rgb(255, 255, 255);
  border-radius: 10px;
  object-fit: cover;
  overflow: hidden;
}
.work-image img {
  width: 100%;
  height: 100%;
  box-shadow: 0px 0px 30px #0000001c;
  z-index: 1;
  object-fit: cover;
}
/* .work-image img:nth-child(1) {
  left: 50px;
  top: 40%;
} */
.work-content {
  flex-direction: column;
  padding: 0 20px;
  width: 40%;
  overflow: auto;
  scroll-behavior: smooth;
}

.main-content,
.sub-content {
  margin: 15px 0;
}
.company-name {
  font-size: 35px;
  font-weight: bold;
  color: rgb(47, 47, 47);
}
.company-info {
  color: var(--secondery-color);
  font-size: 18px;
}
.used-technology {
  color: var(--secondery-color);
  font-size: 18px;
  margin: 5px 0;
}
.lable {
  font-weight: bold;
  color: rgb(47, 47, 47);
  font-size: 18px;
}
.category {
  color: var(--secondery-color);
  font-size: 18px;
  margin: 5px 0;
}
.btn-div {
  margin: 20px;
  width: 100%;
  text-align: center;
}
.visit-button {
  width: 30%;
}
.slid-div {
  display: flex;
  width: 100%;
  height: auto;
  justify-content: center;
  align-items: center;
}

@media (max-width: 1100px) {
  .work-div {
    width: 90%;
    height: 500px;

    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .main-content,
  .sub-content {
    margin: 15px 10px;
  }
  .work-image {
    flex-direction: column;
    width: 100%;
    height: 100%;

    border-radius: 0px;
  }
  .work-image img {
    flex-direction: column;
    width: 100%;
    border-radius: 0px;
  }
  .work-content {
    width: 100%;
    overflow: auto;
  }
}
.work-content::-webkit-scrollbar {
  width: 5px; /* width of the entire scrollbar */
}
.work-content::-webkit-scrollbar-track {
  background: rgb(236, 236, 236); /* color of the tracking area */
}

.work-content::-webkit-scrollbar-thumb {
  background-color: rgb(200, 200, 200); /* color of the scroll thumb */
  border-radius: 10px; /* roundness of the scroll thumb */
  border: 3px solid transparent; /* creates padding around scroll thumb */
}

/* we-work-with */
.we-work-with {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.all-tech {
  padding: 20px;
  width: 70%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  align-items: center;
}
@media (max-width: 1000px) {
  .all-tech {
    width: 70%;
  }
}
@media (max-width: 750px) {
  .all-tech {
    width: 90%;
  }
}
@media (max-width: 450px) {
  .all-tech {
    width: 100%;
  }
}
.Technologies {
  margin: 20px 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  height: 100px;
  width: 140px;
  /* background-color: antiquewhite; */
  border-radius: 10px;
  overflow: hidden;
}
.tech-img {
  height: 70%;
  width: 100%;
  object-fit: contain;
}
.tech-name {
  color: var(--secondery-color);
  font-size: 20px;
}

